






































import FullPageEmptyState from '../../../components/full-page-empty-state.vue'
export default {
	components: {
		"full-page-empty-state": FullPageEmptyState
	},
	data() {
		return {
			isSelect: false,
			addressList: []
		};
	},
	onShow() {
		this.loadMyAddress()
	},
	onLoad(e) {
		if (e.type == 'select') {
			this.isSelect = true;
		}
	},
	methods: {
		edit(row) {
			uni.setStorage({
				key: 'address',
				data: row,
				success() {
					uni.navigateTo({
						url: "edit/edit?type=edit"
					})
				}
			});

		},
		add() {
			uni.navigateTo({
				url: "edit/edit?type=add"
			})
		},
		select(address) {
			//是否需要返回地址(从订单确认页跳过来选收货地址)
			if (!this.isSelect) {
				return;
			}
			uni.setStorage({
				key: 'selectedAddress',
				data: address,
				success() {
					uni.navigateBack();
				}
			})
		},
		loadMyAddress() {
			let that = this
			this.$pin.request('GET', '/commons/user/address', null,
				successData => {
					console.log(successData)
					that.addressList = successData.data.list
				},
				failData => {
					console.log(failData)
					uni.showToast({
						icon: 'none',
						title: '加载地址列表出错'
					})
				}
			)
		}
	}
}
